.about-banner {
  border-radius: 25px;
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: contrast(77%) brightness(87%);
  margin-top: 55px;
}

.about-container {
  width: 95%;
  margin: 0 auto;

}

.about-collapse {
  margin: 40px auto;
}

/****************VERSION MOBILE************************/
@media screen and (max-width: 768px) {
  .about-container {
    width: 100%;
  }

}